body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#App {
  width: 100%;
  height: 100%; }

:root {
  --hue: 0; }

#Background {
  position: absolute;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  background: linear-gradient(180deg, hsl(calc(0 + 30), 100%, 50%), hsl(calc(0 + 60), 100%, 50%));
  background: linear-gradient(180deg, hsl(calc(var(--hue) + 30), 100%, 50%), hsl(calc(var(--hue) + 60), 100%, 50%));
  z-index: 0; }
  #Background .Cell {
    background: linear-gradient(135deg, hsl(calc(0 + 30), 100%, 50%), hsl(calc(0 + 60), 100%, 50%));
    background: linear-gradient(135deg, hsl(calc(var(--hue) + 30), 100%, 50%), hsl(calc(var(--hue) + 60), 100%, 50%));
    width: calc(10vw - 2px);
    height: calc(10vw - 2px);
    -webkit-transform: rotate(33.75deg);
            transform: rotate(33.75deg);
    border: 1px solid hsl(calc(0 + 15), 100%, 50%);
    border: 1px solid hsl(calc(var(--hue) + 15), 100%, 50%); }

#Content {
  position: absolute;
  z-index: 1;
  width: 100%; }
  #Content input {
    width: 100%; }
  #Content output {
    white-space: pre; }

